@charset "UTF-8";
h1,
h2,
h3,
h4,
h5 {
  font-family: "Proza Libre", sans-serif;
  color: #0f393b;
  text-transform: capitalize; }

p,
li,
a {
  font-family: "Open Sans", sans-serif;
  color: #0f393b;
  font-size: 1.2em; }

body {
  background-color: #f6f5ef; }

nav {
  margin-top: 1em; }
  nav .logo {
    width: 40px;
    color: #0f393b;
    margin-left: 2em;
    transition: 1s all ease; }
    nav .logo:hover {
      transform: rotate(360deg); }
  nav .nav-link {
    font-family: "Proza Libre", sans-serif;
    padding: 0.2rem 1rem;
    transition: 0.25s all ease; }
    nav .nav-link:hover {
      color: #ea7f4a;
      transform: translateX(10px); }

.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 4em; }
  .home h1 {
    font-size: 5em; }
  .home h2 {
    font-size: 2em; }
  .home h3 {
    font-size: 2em;
    color: #ea7f4a; }

.btn {
  border-radius: 0;
  padding: 0.5em 0.5em;
  font-size: 25px;
  text-transform: capitalize;
  font-family: "Proza Libre", sans-serif;
  margin: 1em 0;
  transition: 0.2s all ease-in; }
  .btn:focus {
    outline: none;
    box-shadow: none; }

.btn-border {
  background: transparent;
  color: #0f393b; }
  .btn-border:hover, .btn-border:active {
    border-bottom: 3px solid #0f393b;
    background: transparent;
    color: #0f393b;
    letter-spacing: 4px; }
  .btn-border i {
    color: #edcb50;
    font-size: 10px;
    vertical-align: middle; }

.btn-fill {
  background-color: transparent;
  border-radius: 0;
  font-size: 20px;
  border: 5px solid #0f393b;
  color: #0f393b; }
  .btn-fill:hover {
    background-color: #0f393b;
    color: #f6f5ef;
    letter-spacing: 3px; }

.profile h1 {
  font-size: 3.5em; }

.profile .skills {
  text-align: right; }
  .profile .skills .icon-container {
    display: grid;
    justify-items: end;
    grid-template-columns: repeat(auto-fill, minmax(170px, 1fr)); }
    .profile .skills .icon-container img {
      max-width: 100px;
      margin-top: 1em; }
  .profile .skills h2 {
    text-align: right; }
  .profile .skills .skill-text-container {
    display: flex;
    justify-content: flex-end; }
    .profile .skills .skill-text-container p {
      font-size: 1.4em;
      margin-left: 2em;
      text-align: right; }

.profile .experiences .company {
  margin-top: 2em; }
  .profile .experiences .company h3 {
    font-weight: 700; }
  .profile .experiences .company li {
    margin: 0.5em 1em;
    list-style-type: none; }
    .profile .experiences .company li:before {
      content: "â– ";
      font-size: 1em;
      color: #ea7f4a;
      margin-right: 1em; }

.profile .contact {
  display: flex;
  justify-content: space-evenly;
  margin: 4em; }
  .profile .contact img {
    filter: invert(84%) sepia(80%) saturate(6972%) hue-rotate(327deg) brightness(95%) contrast(92%); }

.profile img {
  transition: 0.5s all ease; }
  .profile img:hover {
    transform: scale(1.2); }

.project {
  margin-bottom: 5em; }
  .project .title {
    display: flex;
    justify-content: center;
    align-items: center; }
    .project .title h2 {
      font-size: 9em;
      position: absolute;
      z-index: -1;
      color: transparent;
      -webkit-text-stroke: 2px #ea7f4a;
      font-weight: 600;
      opacity: .5; }
    .project .title h1 {
      font-size: 4em; }
  .project .language-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 2em; }
    .project .language-container li {
      list-style-type: none; }
  .project .img-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); }
    .project .img-container img {
      max-width: 350px; }
  .project .info-container {
    margin-top: 2em; }
    .project .info-container h3 {
      font-weight: 600; }
  .project .btn-container {
    display: flex;
    justify-content: center; }
